@import "../../styles/index";

@import "../../styles/variables";
@import "../../styles/reset";
@import "../../styles/typography";

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-family: var(--f-primary);
  color: var(--c-white);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--c-black);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input[inputmode="decimal"] {
  font-family: var(--f-mono);
}

@import "../../styles/index";

$component: top-bar;

.#{$component} {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 3rem;
    padding: 0.5rem;
    background: var(--c-grey);

    &__menu-button,
    &__airswap-button {
        height: 2rem;
        aspect-ratio: 1;
        color: var(--c-white);
    }

    &__menu-button {
        position: absolute;
        left: calc(50% - 1rem);
    }

    .#{$component}__list-button  {
        border: 1px solid var(--c-light-grey);
        border-radius: .25rem;
        height: 100%;
        padding: 0 .75rem;
        text-transform: uppercase;
        font-size: .75rem;
        font-weight: var(--fw-700);
        color: var(--c-white);
    }

    &__list-button-icon {
        width: 0.75rem;
        height: 0.75rem;
    }
}
