$c-primary: #2B71FF;
$c-white: #FFF;
$c-black: #000;
$c-kuroi-black: #101217;
$c-river-styx: #1A1E25;
$c-light-grey: #6E7686;

@mixin light-mode-variables {
    --c-primary:        #{$c-primary};
    --c-white:          #{$c-black};
    --c-black:          #{$c-white};
}

:root {
    --c-primary: #{$c-primary};
    --c-white: #{$c-white};
    --c-white-always: #{$c-white};
    --c-black: #{$c-black};
    --c-black-always: #{$c-black};
    --c-dark: #{$c-kuroi-black};
    --c-dark-border: #{$c-river-styx};
    --c-grey: #{$c-river-styx};
    --c-light-grey: #{$c-light-grey};

    --f-primary: "loos-normal", sans-serif;
    --f-mono: "DM Mono", monospace;

    --page-padding: 1.5rem;

    // loos-normal font-weights
    --fw-800: 800;
    --fw-700: 700;
    --fw-500: 500;
    --fw-400: 400;
    --fw-300: 300;
    --fw-200: 200;
    --fw-100: 100;

    // Disable this for now since the default theme is dark
    // @media (prefers-color-scheme: light) {
    //     @include light-mode-variables;
    // }

    &[data-theme="light"] {
        @include light-mode-variables;
    }
}
